<template>
  <el-card class="box-card" style="height: 350px">
    <div slot="header" class="clearfix">
      <span>小程序</span>
      <el-button style="float: right;" size="mini" @click="updateSetting" type="primary">保存配置</el-button>
    </div>
    <div class="text item">
      <el-form :model="setting"  ref="settingForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="appId" class="border">
          <el-input v-model="setting[0].code" value="appletAppId"></el-input>
          <el-input v-model="setting[0].val"></el-input>
        </el-form-item>

<!--        <div class="layui-input-block">-->
<!--          <input type="hidden" name="configs[0].code" value="appletAppId">-->
<!--          <input type="hidden" name="configs[0].name" value="小程序AppId">-->
<!--          <input type="text" name="configs[0].val" autocomplete="off"-->
<!--                 value="${configs.appletAppId}" class="layui-input">-->
<!--        </div>-->


        <el-form-item label="appSecret" class="border">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="记录日志" class="border">
          <el-switch></el-switch>
        </el-form-item>
      </el-form>
    </div>
  </el-card>
</template>
<script>
  export default {
    name: "base-setting",
    props: {
      configs: {
        type: Object,
        default: null
      }
    },
    data() {
      return {
        //表单校验
        rules: {
        },
        setting:[{code:'',name:'',val:''}],
      };
    },
    mounted() {
      let configs = this.configs;
      console.log(configs)
    },
    methods: {
      updateSetting(){
        this.$emit("updateSetting",this.setting)
      }
    }
  };
</script>



